import { Vector3 } from "three";
import { AudioSource } from "./audioSource";
import * as Tone from "tone";
import { Melody } from "../grammars/Melody";

export class SparseNotes extends AudioSource {
  private synth: Tone.Synth

  constructor(
    position: number,
    root: string,
    attack: number,
    decay: number,
    sustain: number,
    release: number,
    duration: string,
    rhythmicStructure: string
  ) {
    super(position, rhythmicStructure)

    this.root = root;
    this.envelope = {
      attack: attack,
      decay: decay,
      sustain: sustain,
      release: release,
    };
    this.noteDuration = duration

    this.melody = new Melody(0)

    this.synth = new Tone.Synth({
      oscillator: {
        type: 'triangle'
      },
      envelope: {
        attack: this.envelope.attack,
        decay: this.envelope.decay,
        release: this.envelope.release,
        sustain: this.envelope.sustain,
      }
    });
    this.filter = new Tone.Filter(1200, "lowpass")
    this.delay = new Tone.PingPongDelay("8n", 0.2)
    this.reverb = new Tone.Reverb({decay: 20.0, wet:1})
    this.gain = new Tone.Gain(0.5).toDestination()
    
    this.synth.chain(this.filter, /*this.delay,*/ this.reverb, this.panner, this.gain)  //synth -> filter -> delay -> reverb -> gain

    this.initializeLoop()
  }

  initializeLoop() {
    this.loop = new Tone.Loop((time) => {
      if (this.rhythmicStructure[this.count] == "1") {
        let next = this.melody.nextNote()
        this.synth.triggerAttackRelease(Tone.Frequency(this.root).transpose(next).toFrequency(), this.noteDuration)
      }
      this.count == this.rhythmicStructure.length - 1 ? (this.count = 0) : this.count++;
    }, "1m")    //Loop duration
  }

  play() {
    if(!this.isPlaying){
      this.loop.start()
      this.isPlaying = true
    }
  }

  stop() {
    if(this.isPlaying){
      this.loop.stop()
      this.isPlaying = false
    }
  }
  
  openFilter(amt: number){
    this.filter.frequency.value =  1200 + amt * 500.0;
  }
}
