import { Vector3 } from "three";
import { AudioSource } from "./audioSource";
import * as Tone from "tone";
import { Sampler } from "tone";
import noise1 from "../../assets/samples/noise1.mp3"
import noise2 from '../../assets/samples/noise2.mp3'
import grain1 from "../../assets/samples/grain1.mp3";

export class NoiseGenerator extends AudioSource {
  private sampler: Tone.Sampler;
  private grainplayer: Tone.GrainPlayer;

  private sampleList: string[] = ['A1', 'B1', 'C1']

  constructor(position: number,  duration: string, rhythmicStructure: string) {
    super(position, rhythmicStructure)

    this.sampler = new Sampler({
      urls: {
        A1: noise1,
        B1: noise2,
      }   
    })

    this.grainplayer = new Tone.GrainPlayer(grain1)
    this.grainplayer.loop = true;
    this.grainplayer.grainSize = 0.02
    this.grainplayer.playbackRate =  0.3;
    this.grainplayer.overlap = 0.05;

    this.gain = new Tone.Gain(0.6).toDestination()
    this.delay = new Tone.PingPongDelay('16n', 0.6)
    this.reverb = new Tone.Reverb({decay: 1.0, wet:0.3})

    this.grainplayer.chain(this.delay, this.reverb, this.gain)
    this.sampler.chain(this.delay, this.reverb, this.gain)

    this.noteDuration = duration

    this.initializeLoop()
  }

  initializeLoop() {
    this.loop = new Tone.Loop((time) => {
      if (this.rhythmicStructure[this.count] == "1") {
        let idx = Math.floor(Math.random() * this.sampleList.length)
        this.sampler.triggerAttackRelease(this.sampleList[idx], this.noteDuration)
      } 
      
      else if (this.rhythmicStructure[this.count] == "2") { //trigger granular synth
        this.grainplayer.loopStart = Math.random() * 5.0
        this.grainplayer.start()
        this.grainplayer.stop(this.noteDuration)
      }
      this.count == this.rhythmicStructure.length - 1 ? (this.count = 0) : this.count++;
    }, "2n");   //Loop duration
  }
  
  play(){
    if(!this.isPlaying){
      this.loop.start()
      this.isPlaying = true
    }
  }

  stop(){
    if(this.isPlaying){
      this.loop.stop()
      this.grainplayer.stop()
      this.isPlaying = false
    }
  }

  openFilter(amt: number){/*do nothing*/}
  changeMode(mode: string) {/*do nothing*/}
  getMode(): string {return ""}
}